@import "../Variables.scss";

#TopNav {
  display: flex;
  background-color: $lightGray;
  a {
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    text-decoration: none;
    color: black;
    background-color: $lightBlue;
